@charset "UTF-8";
/* **************** COMMON ****************** */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  line-height: 1;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
sup {
  vertical-align: super;
}
img {
  border: none;
  vertical-align: middle;
}
ol,
ul {
  list-style: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption,
th,
td {
  text-align: left;
  font-weight: normal;
  vertical-align: middle;
}
q,
blockquote {
  quotes: none;
}
q::before,
blockquote::before,
q::after,
blockquote::after {
  content: "";
  content: none;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}
a:visited {
  text-decoration: none;
}
a:hover,
a:active {
  outline: 0;
  text-decoration: underline;
}
button {
  border: none;
}
@font-face {
  font-family: 'Roboto';
  src: url('../fonts/Roboto/Roboto-Regular.eot');
  src: url('../fonts/Roboto/Roboto-Regular.eot?#iefix') format('embedded-opentype'), url('../fonts/Roboto/Roboto-Regular.woff2') format('woff2'), url('../fonts/Roboto/Roboto-Regular.woff') format('woff'), url('../fonts/Roboto/Roboto-Regular.ttf') format('truetype'), url('../fonts/Roboto/Roboto-Regular.svg#Roboto-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: block;
}
.clearfix:after {
  clear: both;
}
body {
  background: #303030;
  color: #000;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
}
.img {
  max-width: 100%;
  height: auto;
}
.container {
  margin: 0 auto;
  max-width: 1280px;
}
.main-page {
  background: linear-gradient(45deg, #0b5c56, #053a72);
  display: grid;
}
.section-articles {
  padding: 50px 0 0 0;
}
button {
  border: none;
}
button:hover {
  cursor: pointer;
}
/* **************** MEDIA ****************** */
@media only screen and (max-width: 1400px) {
  .container {
    width: 97%;
  }
}
@media only screen and (max-width: 600px) {
  .container {
    width: 90%;
  }
}
/* **************** MASONRI GRID ****************** */
.grid-item {
  margin-bottom: 30px;
  margin-right: 30px;
  float: left;
  width: 180px;
}
.grid-item_width-2 {
  width: 230px;
}
.grid-item_width-3 {
  max-width: 320px;
  width: 100%;
}
.grid-item_width-4 {
  max-width: 530px;
  width: 100%;
}
.grid-item_width-5 {
  max-width: 680px;
  width: 100%;
}
.grid-item_width-6 {
  max-width: 940px;
  width: 100%;
}
.article-account.grid-item_width-2 {
  width: 233px;
}
.article-account.grid-item_width-2 .card-follow {
  margin-bottom: 30px;
}
/* **************** PAGE COMPONENTS ****************** */
/* **************** CARD MAIN-SLIDER ****************** */
.main-slider {
  padding-bottom: 50px;
  background: #fff;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.slick-arrow {
  cursor: pointer;
  position: absolute;
  bottom: -25px;
  transform: translate(0, -50%);
  z-index: 15;
  transition: background-color 0.3s ease-in-out;
  height: 50px;
  width: 45px;
  color: #fff;
  background-color: #00b2b2;
}
.slick-arrow:hover {
  background-color: #009899;
}
.slick-arrow:active {
  background-color: #007f7f;
}
.slick-arrow::before {
  content: '';
  position: absolute;
  border-top: 2px solid;
  border-right: 2px solid;
  width: 10px;
  height: 10px;
  top: 50%;
  left: 50%;
  transform: translate(-68%, -50%) rotate(45deg);
}
.slick-arrow::after {
  content: "";
  background: transparent;
  border-radius: 50%;
  border: 1px solid #fff;
  height: 30px;
  width: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.slick-prev {
  left: 0;
}
.slick-prev::before {
  transform: translate(-35%, -50%) rotate(-135deg);
}
.slick-next {
  right: 0;
}
.slick-dots {
  bottom: 16px;
  position: absolute;
  text-align: center;
  width: 100%;
}
.slick-dots li {
  background-color: #00b2b2;
  border: 2px solid #00b2b2;
  border-radius: 50%;
  display: inline-block;
  height: 13px;
  width: 13px;
  margin-right: 11px;
  transition: all 0.3s ease-in-out;
}
.slick-dots li:hover {
  cursor: pointer;
}
.slick-dots li.slick-active {
  background-color: #fff;
}
.slick-dots li:last-child {
  margin-right: 0;
}
.slick-dots li button {
  display: none;
}
.main-slider-item__img img {
  max-height: 200px;
  max-width: none;
  width: 100%;
}
/* **************** ARTICLE SOCIALS ****************** */
.article-socials {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 100px 100px;
}
.socials-item {
  background: #00acee;
  cursor: pointer;
  display: inline-block;
  height: 110px;
  position: relative;
  transition: transform 0.2s ease-in-out;
  width: 100px;
}
.socials-item:hover {
  transform: scale(0.9);
}
.socials-item:active {
  transform: scale(0.85);
}
.socials-item .social-icon {
  color: #fff;
  font-size: 63px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.dr {
  background: #ed4a89;
}
.gl {
  background: #d6492e;
}
.fb {
  background: #3b589c;
}
/* **************** ARTICLE PERSON ****************** */
.article-person {
  background: #fff;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.article-person .article-person-content {
  height: 56px;
  padding: 10px;
}
.article-person .article-person-content .article-title {
  color: #696969;
  font-size: 14px;
  line-height: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 8px;
}
.article-person .article-person-content .article-link {
  color: #696969;
}
.article-person .article-person-content .article-posted {
  color: #03b1bc;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.article-person .article-person-action {
  border-top: 0;
  display: flex;
  justify-content: space-between;
  padding: 0;
}
.article-person .person-action {
  background: #00b2b2;
  border-right: 1px solid #6be5e6;
  border-radius: 0;
  padding: 0;
  height: 34px;
  line-height: 34px;
  width: 33.33%;
  transition: background 0.3s ease-in-out;
  outline: none;
}
.article-person .person-action:last-child {
  border-right: 0;
}
.article-person .person-action:focus {
  background-color: #00b2b2;
}
.article-person .person-action:hover {
  background: #009899;
}
.article-person .person-action > * {
  display: inline-block;
  font-size: 12px;
  vertical-align: middle;
}
.article-person .person-action-txt {
  color: #6be5e6;
}
.article-person .person_icon {
  color: #6be5e6;
}
.article-person-img {
  max-height: 160px;
  overflow: hidden;
}
/* **************** CARD NAV ****************** */
.article-nav {
  border-radius: 0;
  display: flex;
  justify-content: space-between;
  background: #fff;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.article-nav .nav {
  width: 66%;
}
.article-nav .search {
  padding: 18px 15px 0 0;
  width: 34%;
}
.article-nav .side-nav li > a {
  color: #000;
  height: 48px;
  line-height: 48px;
  padding: 0 32px;
  display: block;
  transition: background 0.2s, padding 0.3s;
  font-size: 14px;
}
.article-nav .side-nav li > a:hover {
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.05);
  padding-left: 37px;
}
.deskt-menu > li {
  height: 76px;
  float: left;
  border-right: 1px solid #d5d5d5;
}
.deskt-menu__item {
  background: transparent;
  color: #414141;
  display: inline-flex;
  align-items: center;
  height: 100%;
  line-height: 1;
  padding: 0 25px;
  transition: all 0.2s ease-in-out;
}
.deskt-menu__item:hover {
  background: #eee;
  text-decoration: none;
}
.nav__mobile-btn {
  display: none;
  background: #26a69a;
  text-align: center;
  letter-spacing: .5px;
  transition: 0.2s ease-out;
  text-transform: uppercase;
  box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  outline: none;
  color: #fff;
  font-size: 14px;
  height: auto;
  line-height: 1;
  padding: 15px;
  margin: 18px 0 0 15px;
}
.nav__mobile-btn:hover {
  background: #2bbbad;
}
.menu_wrap {
  text-transform: uppercase;
  transform: translateX(-100%);
  position: fixed;
  max-width: 280px;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  transition: transform 0.4s, padding 0.3s;
  z-index: 102;
  background: #fff;
}
.menu_wrap li {
  border-bottom: 1px solid #d5d5d5;
}
.menu_mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 101;
  background: #000;
  opacity: 0;
}
.menu_active .menu_wrap {
  transform: translateX(0);
}
.menu_close {
  position: absolute;
  top: -20px;
  left: 7px;
  cursor: pointer;
}
.search .search-btn {
  border-radius: 0;
  box-shadow: none;
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  line-height: 40px;
  height: 40px;
  width: 40px;
  text-decoration: none;
  background-color: #00b2b2;
  text-align: center;
  letter-spacing: .5px;
  transition: background 0.2s;
  outline: none;
}
.search .search-btn:active {
  background-color: #007f7f;
}
.search .search_icon {
  color: #fff;
}
.search .search-input {
  padding-right: 45px !important;
  background-color: #eee;
  border: 2px solid #eee;
  outline: none;
  line-height: 40px;
  height: 40px;
  width: 100%;
  font-size: 1rem;
  margin: 0;
  padding: 0 15px;
  box-shadow: none;
  transition: all .3s;
}
.search .input-field {
  position: relative;
  margin-bottom: 1rem;
}
.search .input-label {
  color: #9e9e9e;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  font-size: 1rem;
  cursor: text;
  transform-origin: 0 100%;
  transform: translateY(12px);
  transition: transform 0.2s ease-out;
  text-align: initial;
  margin: 0 15px;
  pointer-events: none;
}
.search .input-label.active {
  transform: translateY(-14px) scale(0.8);
  color: #00b2b2;
  transform-origin: 0 0;
}
/* **************** MEDIA ****************** */
@media only screen and (max-width: 992px) {
  .article-nav .search {
    width: 60%;
  }
  .nav__mobile-btn {
    display: block;
  }
  .deskt-menu {
    display: none;
  }
}
/* **************** ACCORDEON ****************** */
.accordeon {
  min-height: 278px;
}
.accordeon a:hover {
  text-decoration: none;
}
.accordeon_trigger {
  text-decoration: none;
  background: #00b2b2;
  display: inline-block;
  color: #bcffff;
  border-bottom: 1px solid #00808b;
  width: 100%;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.accordeon_trigger_span {
  font-size: 15px;
}
.accordeon_trigger_span-2 {
  border-radius: 50%;
  border: 1px solid #78e7ee;
  font-size: 12px;
  position: relative;
  margin-right: 7px;
  height: 24px;
  width: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.accordeon_trigger_span-2::after {
  content: "";
  display: inline-block;
  border: 5px solid transparent;
  border-top: 5px solid #bcffff;
  position: absolute;
  right: -20px;
  top: 66%;
  transform: translateY(-50%);
  transition: all 0.2s ease-in-out;
}
.accordeon_item {
  display: block;
}
.accordeon_item.active .accordeon_trigger_span-2::after {
  border-top: none;
  border-bottom: 4px solid #2e2e2e;
  border-bottom-color: #fff;
  top: 50%;
}
.accordeon_inner {
  display: none;
}
.accordeon_inner-link {
  text-decoration: none;
  color: #828282 ;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0 12px 20px;
}
.accordeon_inner-link_span-2 {
  border-radius: 50%;
  border: 1px solid #828282;
  font-size: 12px;
  position: relative;
  margin-right: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 24px;
  width: 24px;
}
.accordeon_inner-item {
  display: block;
  background: #fff;
  border-bottom: 1px solid #00808b;
  transition: padding 0.3s;
}
.accordeon_inner-item:hover {
  padding-left: 5px;
}
/* **************** CARD FOLLOW ****************** */
.follow {
  background: #fff;
  border-radius: 0;
  padding: 22px 10px;
  text-align: center;
  margin-bottom: 30px;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.follow__img {
  height: 147px;
  margin-bottom: 15px;
}
.follow__img .img {
  border: 5px solid #fff;
  border-radius: 50%;
  display: inline-block;
  height: 140px;
  width: 140px;
}
.follow-img__link {
  border-radius: 50%;
  border: 1px solid #00b2b2;
  display: inline-block;
}
.follow__title {
  margin-bottom: 7px;
  max-width: 200px;
  line-height: 1;
}
.follow__title .follow-link {
  color: #5b5b5b;
  font-size: 14px;
}
.follow__title .follow-link:hover {
  text-decoration: none;
}
.follow__nicname {
  color: #5ab8b7;
  font-size: 11px;
  margin-bottom: 20px;
  max-width: 200px;
}
.follow__btn {
  color: #a6fcff;
  text-transform: none;
  background: #26a69a;
  font-size: 16px;
  border-radius: 2px;
  line-height: 39px;
  padding: 0 27px;
  outline: none;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
  transition: 0.2s ease-out;
}
.follow__btn:hover {
  background: #2bbbad;
}
.follow_icon {
  vertical-align: middle;
  font-size: 21px;
}
.article-dropdown .dropdown-wrapper {
  position: relative;
  margin-bottom: 20px;
  border: 0;
}
.article-dropdown .dropdown-button {
  background: #fff;
  border-radius: 0;
  color: #6a6a6a;
  overflow: visible;
  padding: 0 35px 0 10px;
  position: relative;
  width: 100%;
  text-transform: none;
  text-align: left;
  height: 36px;
  line-height: 36px;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
  border: none;
  outline: none;
}
.article-dropdown .dropdown-button::after {
  content: '';
  display: inline-block;
  border: 4px solid transparent;
  border-bottom: 7px solid transparent;
  position: absolute;
  right: 20px;
  bottom: -10px;
  z-index: 2;
  transition: all 0.1s ease-in-out;
}
.article-dropdown .dropdown-button.show {
  background: #26a69a;
  color: #94d9e8;
}
.article-dropdown .dropdown-button.show::after {
  border-bottom-color: #fff;
}
.article-dropdown .dropdown-button.show .badge-item {
  border-left-color: transparent;
}
.article-dropdown .dropdown-button.show .badge-item::before {
  border-color: #94d9e8;
}
.article-dropdown .dropdown-button.show .badge-item::after {
  border-top: none;
  border-bottom: 4px solid #2e2e2e;
  border-bottom-color: #fff;
  top: 50%;
}
.article-dropdown .dropdown-button .badge-item {
  border-left: 1px solid #dadada;
  display: inline-block;
  height: 100%;
  width: 35px;
  position: absolute;
  top: 0;
  right: 0;
}
.article-dropdown .dropdown-button .badge-item::before {
  content: '';
  background: transparent;
  border: 1px solid #58585a;
  border-radius: 50%;
  display: inline-block;
  height: 22px;
  width: 22px;
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.article-dropdown .dropdown-button .badge-item::after {
  content: '';
  border: 4px solid transparent;
  border-top: 4px solid #2e2e2e;
  display: inline-block;
  transition: all 0.3s ease-in-out;
  position: absolute;
  top: 59%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
.article-dropdown .dropdown-content {
  margin-top: 10px;
  display: none;
  position: absolute;
  width: 100%;
  z-index: 3;
}
.article-dropdown .dropdown-content.show {
  display: block;
  width: 100%;
  opacity: 1;
}
.article-dropdown .dropdown-content > li {
  border-bottom: 2px solid #a1c0bb;
  background: #fff;
  min-height: 50px;
  transition: padding 0.3s;
}
.article-dropdown .dropdown-content > li:hover {
  padding-left: 5px;
}
.article-dropdown .dropdown-content > li:last-child {
  border-bottom: 0;
}
.article-dropdown .dropdown-content li > a {
  padding: 14px 16px;
  display: block;
}
.article-dropdown .dropdown-content .dropdown-content__item {
  color: #767676;
}
.article-dropdown .dropdown-content .dropdown-content__item:hover {
  text-decoration: none;
}
/* **************** ARTICLE WEATHER ****************** */
.article-weather {
  background: transparent;
  max-height: 255px;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.article-weather .tabs {
  background: transparent;
  height: auto;
  white-space: normal;
  display: flex;
  justify-content: space-between;
}
.article-weather .tab {
  height: auto;
  line-height: 1;
  width: 24.5%;
}
.article-weather .days-list__item {
  background: #00b2b2;
  color: #fff;
  display: block;
  font-size: 12px;
  text-align: center;
  text-transform: none;
  transition: all 0.2s ease-in-out;
  padding: 13px 5px;
}
.article-weather .days-list__item.active,
.article-weather .days-list__item:hover {
  background: #029494;
  text-decoration: none;
  color: #fff;
}
.article-weather .days-list__item .img {
  display: inline-block;
  font-size: 20px;
  margin-bottom: 5px;
}
.article-weather .days-list__item .txt {
  display: block;
}
.article-weather .indicator {
  display: none !important;
}
.article-weather__day {
  background: #00b2b2;
  margin-bottom: 2px;
  padding: 15px;
  display: none;
}
.day-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
}
.day-info__city {
  color: #93cee0;
}
.temp-format__item {
  color: #0b6678;
  cursor: pointer;
  margin-left: 5px;
}
.temp-format__item.active {
  color: #93cee0;
}
.temp-format__item.active .circle {
  border-color: #93cee0;
}
.day-main {
  color: #fff;
  padding: 40px 0;
  text-align: center;
}
.day-main__img {
  display: inline-block;
  font-size: 62px;
  margin-right: 35px;
  vertical-align: middle;
}
.day-main__temp {
  display: inline-block;
  vertical-align: middle;
  font-size: 62px;
}
.day-main__temp .circle {
  border-color: #fff;
  height: 11px;
  width: 11px;
}
.day-main__temp .num {
  display: none;
}
.day-main__temp .num.active {
  display: inline-block;
}
/* **************** ARTICLE CALENDAR ****************** */
.article-calendar {
  background: transparent;
  border-radius: 0;
  min-height: 255px;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.article-calendar .ui-datepicker {
  background: #fff;
  border: 0;
  font-family: 'Roboto', Arial, sans-serif;
  padding: 0;
  width: 100%;
}
.article-calendar .ui-datepicker-header {
  background: #00b2b2;
  border: 0;
  border-radius: 0;
  padding: 30px 15px;
}
.article-calendar .ui-datepicker-title {
  color: #bbffff;
  font-size: 14px;
  text-transform: none;
}
.article-calendar .ui-datepicker-prev,
.article-calendar .ui-datepicker-next {
  background: transparent;
  border-radius: 50%;
  border: 1px solid #bbffff;
  height: 30px;
  width: 30px;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  transition: all 0.2s ease-in-out;
}
.article-calendar .ui-datepicker-prev:focus,
.article-calendar .ui-datepicker-next:focus {
  background-color: #00bbb3;
}
.article-calendar .ui-datepicker-prev:hover,
.article-calendar .ui-datepicker-next:hover {
  background-color: #049c96;
}
.article-calendar .ui-datepicker-prev::before,
.article-calendar .ui-datepicker-next::before {
  content: '';
  display: inline-block;
  width: 10px;
  height: 10px;
  border-top: 2px solid #bbffff;
  border-right: 2px solid #bbffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-66%, -50%) rotate(45deg);
}
.article-calendar .ui-datepicker-prev > span,
.article-calendar .ui-datepicker-next > span {
  display: none;
}
.article-calendar .ui-datepicker-prev {
  left: 15px;
  transform: translate(0, -50%) rotate(180deg);
}
.article-calendar .ui-datepicker-next {
  right: 15px;
}
.article-calendar .ui-datepicker-calendar {
  border: 1px solid #bbdfeb;
  margin: 0;
}
.article-calendar th {
  color: #4bb9ae;
  font-size: 14px;
  padding: 15px 6px;
  text-align: center;
}
.article-calendar td {
  padding: 7px 6px;
  text-align: center;
}
.article-calendar .ui-state-default {
  background: transparent;
  border-radius: 50%;
  border: 1px solid transparent;
  color: #717171;
  font-size: 13px;
  display: inline-block;
  text-align: center;
  transition: all 0.2s ease-in-out;
  line-height: 22px;
  height: 28px;
  width: 28px;
}
.article-calendar .ui-state-default.ui-state-highlight {
  border-color: #717171;
}
.article-calendar .ui-state-default.ui-state-active {
  background: #eee;
}
/* **************** ARTICLE ACTIONS ****************** */
.article-actions {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.switch {
  position: relative;
  display: inline-block;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 2px solid #7792af;
}
.switch-item {
  height: 25px;
  width: 39px;
  position: relative;
}
.switch-item_text {
  color: #626262;
  display: inline-block;
  font-size: 14px;
  padding-left: 20px;
}
.switch input {
  display: none;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}
.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 1px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}
input:checked + .slider {
  background-color: #84c7c1;
}
input:checked + .slider:before {
  background-color: #26a69a;
}
input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider:before {
  transform: translateX(15px);
}
#slider-range {
  width: 3em;
  height: 1.6em;
  top: 50%;
  margin-top: -0.8em;
  text-align: center;
  line-height: 1.6em;
}
.irs {
  left: 50%;
  transform: translate(-50%);
  height: 43px;
}
.js-irs-0 {
  width: 86%;
}
.js-irs-1 {
  width: 83%;
  left: 51%;
  margin-bottom: 10px;
}
.irs-line {
  height: 4px;
  background: #cdcdcd;
  margin: 0 auto;
  width: 85%;
  border-radius: 0;
  top: 28px;
}
.irs-bar {
  background: #26a69a;
  height: 4px;
  border: none;
  top: 28px;
}
.irs-slider {
  top: 22px;
  width: 16px;
  height: 16px;
  background: #fff;
  border-color: #fefefe;
}
.irs-from,
.irs-to,
.irs-single {
  background: #26a69a;
  font-size: 10px;
  top: 3px;
  padding: 1px 3px;
}
.irs-bar-edge {
  display: none;
}
/* **************** ARTICLE MESSAGES ****************** */
.article-messages {
  background: #fff;
}
.article-messages.grid-item {
  border-radius: 0;
  margin-right: 25px;
  padding: 20px;
  box-shadow: 1px 3px 3px 0 rgba(0, 0, 0, 0.2);
}
.messages-list__item {
  margin-bottom: 20px;
}
.messages-list__item:last-child {
  margin-bottom: 0;
}
.messages-list__item {
  background: transparent;
  border-left: 3px solid transparent;
  position: relative;
  font-size: 12px;
  padding: 12px 32px 12px 9px;
}
.messages-list__item.success {
  background: #def0d8;
  border-color: #d5e5cb;
  color: #92af7f;
}
.messages-list__item.attention {
  background: #d9edf6;
  border-color: #b0e8e5;
  color: #5b9ea4;
}
.messages-list__item.warning {
  background: #fbfade;
  border-color: #f9efd4;
  color: #e0c38b;
}
.messages-list__item.err {
  background: #f2dedf;
  border-color: #e8d6d6;
  color: #b59097;
}
.messages-list__item .messages-icon {
  font-size: 25px;
  position: absolute;
  top: 50%;
  right: 9px;
  transform: translate(0, -50%);
  z-index: 2;
}
/* **************** ARTICLE FORM ****************** */
.article-form {
  padding: 20px 20px 0;
  background: #fff;
}
.article-form .row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.input-field {
  position: relative;
  margin-bottom: 16px;
}
.input-field .input-label {
  position: absolute;
  top: 0;
  left: 0;
  color: #9e9e9e;
  font-size: 16px;
  transition: transform 0.2s ease-out;
  transform: translateY(12px);
  pointer-events: none;
  margin: 0 15px;
}
.input-field .input-label.active {
  transform: translateY(-14px) scale(0.8);
  color: #00b2b2;
  transform-origin: 0 0;
}
.username {
  width: 45%;
}
.password {
  width: 50%;
}
.input,
.textarea {
  width: 100%;
}
.email {
  width: 70%;
}
.input-active {
  width: 25%;
}
.message {
  width: 100%;
}
.input {
  background-color: #eee;
  border: 2px solid #eee;
  outline: none;
  line-height: 40px;
  height: 40px;
  margin: 0;
  padding: 0 15px;
  box-shadow: none;
  transition: all .3s;
}
.textarea {
  min-height: 80px;
}
.sbmt-btn {
  border-radius: 0;
  color: #77deef;
  position: absolute;
  bottom: 0;
  right: 0;
  text-decoration: none;
  background-color: #26a69a;
  text-align: center;
  letter-spacing: .5px;
  font-size: 16px;
  height: 36px;
  line-height: 36px;
  padding: 0 2rem;
  transition: 0.2s ease-out;
}
.sbmt-btn:hover {
  background: #2bbbad;
}
.input-field input.input:focus {
  background: #fff;
}
.input-field input.error {
  background: #f2dedf;
  border-color: #c99ea5;
}
.input-field input.error:focus {
  background: #fff;
}
.input-field input.error ~ .input-label {
  display: none;
}
.input-field label.error {
  padding-left: 15px;
  position: absolute;
  top: 0;
  left: 0;
  color: #9e9e9e;
  font-size: 15px;
  transition: transform 0.2s ease-out;
  transform: translateY(12px);
  pointer-events: none;
  margin: 0 15px;
}
.input-field label.error.active {
  transform: translateY(-14px) scale(0.8);
  color: #00b2b2;
  transform-origin: 0 0;
}
/* **************** MEDIA ****************** */
@media only screen and (max-width: 600px) {
  .article-form .row {
    flex-direction: column;
  }
  .article-form .input-field {
    width: 100%;
  }
}
