/* **************** CARD FOLLOW ****************** */
.follow {
	background: #fff;
	border-radius: 0;
	padding: 22px 10px;
	text-align: center;
	margin-bottom: 30px;
	box-shadow: 1px 3px 3px 0 rgba(0,0,0,.2);
}

.follow__img {
	height: 147px;
	margin-bottom: 15px;

	.img {
		border: 5px solid #fff;
		border-radius: 50%;
		display: inline-block;
		height: 140px;
		width: 140px;
	}
}

.follow-img__link {
	border-radius: 50%;
	border: 1px solid #00b2b2;
	display: inline-block;
}

.follow__title {
	margin-bottom: 7px;
	max-width: 200px;
	line-height: 1;

	.follow-link {
		color: #5b5b5b;
		font-size: 14px;

		&:hover {
			text-decoration: none;
		}
	}
}

.follow__nicname {
	color: #5ab8b7;
	font-size: 11px;
	margin-bottom: 20px;
	max-width: 200px;
}

.follow__btn {
	color: #a6fcff;
	text-transform: none;
	background: #26a69a;
	font-size: 16px;
	border-radius: 2px;
	line-height: 39px;
	padding: 0 27px;
	outline: none;
	box-shadow: 2px 2px 4px 0 rgba(0,0,0,.5);
	transition: .2s ease-out;
	&:hover {
		background: lighten(#26a69a, 5%); 
	}
}

.follow_icon {
	vertical-align: middle;
	font-size: 21px;
}

.article-dropdown {
	.dropdown-wrapper {
		position: relative;
		margin-bottom: 20px;
		border: 0;
	}

	.dropdown-button {
		background: #fff;
		border-radius: 0;
		color: #6a6a6a;
		overflow: visible;
		padding: 0 35px 0 10px;
		position: relative;
		width: 100%;
		text-transform: none;
		text-align: left;
		height: 36px;
		line-height: 36px;
		font-size: 16px;
		transition:all .3s ease-in-out;
		border: none;
		outline: none;
		&::after {
			content: '';
			display: inline-block;
			border: 4px solid transparent;
			border-bottom: 7px solid transparent;
			position: absolute;
			right: 20px;
			bottom: -10px;
			z-index: 2;
			transition:all 0.1s ease-in-out;
		}
		&.show {
			background: #26a69a;
			color: #94d9e8;
			&::after {
				border-bottom-color: #fff
			}

			.badge-item {
				border-left-color: transparent;

				&::before {
					border-color: #94d9e8;
				}

				&::after {
					border-top: none;
					border-bottom: 4px solid #2e2e2e;
					border-bottom-color: #fff;
					top: 50%;
				}
			}
		}

		.badge-item {
			border-left: 1px solid #dadada;
			display: inline-block;
			height: 100%;
			width: 35px;
			position: absolute;
			top: 0;
			right: 0;

			&::before {
				content: '';
				background: transparent;
				border: 1px solid #58585a;
				border-radius: 50%;
				display: inline-block;
				height: 22px;
				width: 22px;
				z-index: 1;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}

			&::after {
				content: '';
				border: 4px solid transparent;
				border-top: 4px solid #2e2e2e;
				display: inline-block;
				transition: all .3s ease-in-out;
				position: absolute;
				top: 59%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: 2;
			}
		}
	}

	.dropdown-content {
		margin-top: 10px;
		display: none;
		position: absolute;
		width: 100%;
		z-index: 3;
		&.show {
			display: block;
			width: 100%;
			opacity: 1;
		}

		> li {
			border-bottom: 2px solid #a1c0bb;
			background: #fff;
			min-height: 50px;
			transition: padding 0.3s;
			&:hover {
				padding-left: 5px;
			}
			&:last-child {
				border-bottom: 0;
			}
		}

		li > a {
			padding: 14px 16px;
			display: block;
		}

		.dropdown-content__item {
			color: #767676;
			&:hover {
				text-decoration: none;
			}
		}
	}
}